<template>
    <div class="flex">
        <div class="w-1/3">
        {{usersIndex}}
               
        </div>      
        <div class="w-1/3 flex flex-col items-center">
            <img class="w-12 h-12 mt-12" src="@/assets/imgs/icons/favicon.svg">
         <p class="text-xl font-semibold text-blue-500 text-center mt-10 mb-4" v-if="ui.login">THE WORKSMAN</p>
         <p class="text-xl font-semibold text-blue-500 text-center mt-10 mb-5" v-else>THE WORKSMAN USER REGISTRATION</p>
        <p class="text-green-400 font-light text-lg p-5 mb-5" v-if="ui.success"><span v-if="!user.username">{{ui.success}}</span></p>

         <p class="bg-white text-red-600 font-semibold text-xs p-1" v-if="ui.message">{{ui.message}}</p>
          <!--p class="bg-purple-700 text-purple-200 p-2 m-5 rounded-lg" :value="ui.message=null" v-if="authed_user.message">
              <span :value="ui.warn=true">{{authed_user.message}}</span></p-->
          
        <section class="flex flex-col mt-4 rounded-lg" v-if="ui.login"> 
            <div>
                <input type="text" class="input w-full mb-5" placeholder="Username" v-on:focusin="ui.warn=false" :focusout="setCookieId()" v-model.lazy="user.username">
            </div>
            <div >
                <input type="password" class="input w-full" placeholder="Password" v-on:focusin="ui.warn=false" v-model.lazy="user.password">
            </div>
            <button class="btn-primary py-2 px-3 mt-10" type="submit" @click="logIn">LOGIN</button>
            <div>                
                <p class="bg-white text-blue-600 font-semibold text-sm p-1 cursor-pointer" @click="ui.login = !ui.login">or register</p>
                    {{cookieid}}
                        <ul v-if="user.username">
                        <li v-if="usernames.has(user.username)"><span class="text-xl text-green-400 font-bold"><fadeOut info="Ok" directive="success" :delay="ui.fade.fast" /></span></li>
                        <li v-else><span class="text-xl text-red-400 font-bold"><fadeOut info="Invalid Username!" directive="danger" :delay="ui.fade.faster" /></span></li>
                        </ul>
                        
               
            </div>
        </section>

        <section class="flex flex-col mt-10 rounded-lg" v-if="!ui.login">
            <ul>
                <li  v-for="val, key of account_request.user" :key="key">
                    
                    <div v-if="key !='password'">
                    <input type="text" class="input mb-3 w-full" :placeholder="key" v-on:focus="ui.message=null" v-model.lazy="account_request.user[key]">
                    </div>
                    <div v-else>
                    <input :type="key" class="input mb-3 w-full" :placeholder="key" v-model.lazy="account_request.user[key]">
                    </div>
                    
                </li>
                <li  v-for="val, key of account_request.contact" :key="key">
                    
                    <div>
                    <input type="text" class="input mb-3 w-full" :placeholder="key" v-model.lazy="account_request.contact[key]">
                    </div>
                </li>
                
            </ul>

              
              
            <button class="btn-primary py-2 px-3 mt-5" type="submit" @click="register">REGISTER</button>
            <div>                
                <p class="bg-white text-blue-600 font-semibold text-sm p-1 mt-3 cursor-pointer" @click="ui.login = !ui.login">cancell</p>
               
            </div>
        
        
        </section>
         </div>
        <div class="w-1/3">
        <p class="bg-teal-300 text-teal-800" v-if="usernames">{{usernames}}</p>
        
        </div>
    </div>
</template>

<script>
import { mapState, mapGetters } from 'vuex';
import fadeOut from '@/components/utils/fadeOut.vue';

export default {
    name: "index",
    components: {
        fadeOut
    },
    data(){
        return {
            auid: null,
            count: 0,
            delay: 20000,
            cookieid: null,
        
            user: {
                authed_user: localStorage.getItem("username"),
                username: null,
                password: null
            },
            account_request: {
                user: {
                    username: null,
                    password: null,
                    firstname: null,
                    lastname: null
                },
                contact: {
                    email: null,
                    tel: null,
                    mobile: null
                },
                meta: {
                
                    created: Date.parse(Date())
                }
            },
            ui: {
                login: true,
                message: null,
                success: null,
                warn: true,
                fade: {
                    slow: 20000,
                    fast: 10000,
                    faster: 5000,
                    fastest: 2500
                }
            }
        }
    },
    mounted() {
        this.$store.dispatch('loadUsersIndex')
        this.autoloadUser()
    },
    computed: {
        ...mapState({
        authed_user: state => state.user.user, // Object
        //usernames: state => state.user.usernames, // Set
        session: state => state.sessions.session

        }),
        ...mapGetters({
            usersIndex: 'usersIndex',
            usernames: 'usernames',
            currentUser: 'user'
        })
    },
    watch: {
        count: function register (){
            if( this.count > 2){
                this.registerUser()
                this.count = 0 //Reset count
            }

        },
        cookieid: function () {
            
            if(this.user.username)
            {

                //console.log('REGISTERING USER')
                ///this.$store.dispatch('loadSession', this.user.username)
                //console.log('YOU ARE NOW LOGGED IN', this.session)
            }
        }

    },
    methods : {
         async autoloadUser() {
             console.log('logged in user', this.user.authed_user)
            if(this.user.authed_user){  // if a user enters a valid user name that is already in session
                this.cookieid = this.user.authed_user
                //console.log('REGISTERING USER', this.user.username)                
                //this.$store.dispatch('getUserLocation')
                this.$store.dispatch('loadSession', this.user.authed_user)
                .then(userid => {
                    //console.log('CLIENT GOT FROM SESSION STORE', userid)
                    this.ui.message = 'You are already Logged in !'  
                    
                        this.$router.push('/userhome/' + userid)
                    }).catch(error => {
                        console.log('USER IS NOT LOGGED IN', error)
                    })
            }
        
        },
        setCookieId() {
            
            if(this.user.username){  // if a user enters a valid user name that is already
                this.cookieid = this.user.username
                console.log('REGISTERING USER', this.user.username)                

                this.$store.dispatch('loadSession', this.user.username)
                .then(userid => {
                    console.log('CLIENT GOT FROM SESSION STORE', userid)
                    this.ui.message = 'You are already Logged in !'  
                    
                        this.$router.push('/userhome/' + userid)
                    }).catch(error => {
                        console.log('USER IS NOT LOGGED IN', error)
                    })
            }
        
        },
        logIn(){                       
            if ( this.usernames.has(this.user.username) && this.user.password) { // First check for empty request
                console.log('LOGIN GOT', this.user.username, )
              
                try {
                    this.$store.dispatch('authUser', this.user) // yeild user authed username on success
                    .then(username =>{
                        console.log('LOGIN PHASE 2', username)

                        this.ui.message = `AUTHENTICATED USER ${username}`
                        this.$store.dispatch('setSession', username) // Request a new session on the server 

/////////////////////////////////////////////////////////////////////////
                        setTimeout(() => {
                            
                           
                            this.$store.dispatch('loadSession', username) // load the session cookie 
                            .then(cookieid =>{
                                console.log('COOKIE PROMISED TO LOGIN FROM STORE SESSION',cookieid)
                                this.$router.push('/userhome/' + cookieid)
                            })
                            .catch(session_error => {
                                this.ui.message = `YOU ARE NOT AUTORISED TO USE THIS PLATFORM ${session_error}`
                            })
                        }, 10000);
/////////////////////////////////////////////////////////
                       
                    }).catch(error => {
                        this.ui.message = `AUTHED FAILED ${error.message}`
                        console.log('AUTH FAILED', error)
                    }) 
                   
                            
                } catch (error) {
                    this.ui.message = this.authed_user.message
                            
                }
            }                 
            else if ( this.usernames.has(this.user.username) && !this.user.password) {
                 this.ui.message = "You Need to provide a valid Password!"
                 this.count += 1

            }else if ( !this.user.username ) {
                 this.ui.message = "You Need to provide Your Username!"
                 this.count += 1

            }
            else {
                this.ui.message = "Invalid Credentials!"
                this.count += 1
            }       
                    
        },
        registerUser() {
            
                alert(`Hey Hold Up! You Failed ${this.count} Login Attempts. Would you like to Register?`)
                 
                this.ui.login = !this.ui.login // switch interface 
                this.ui.message = null
                this.account_request.user.username = this.user.username // assign username already inputed 
                //this.count = 0 // reset count 

            
        },
        register() {
            if(this.usernames.has(this.account_request.user.username)) {
                this.ui.message = "Sorry That Username is already taken."
                this.account_request.user.username = null
            }else {
                const user = this.account_request.user
                user.contact = this.account_request.contact
                user.meta = this.account_request.meta
                //console.log('REGISTRATION REQUEST FOR', user)
                this.$store.dispatch('registerUser', user)
                this.ui.login = true
                this.ui.success = "Congratulations You are registered to Use The WorksMan. Please login!"
                this.account_request = { // Reset the account requester
                    user: {
                        username: null,
                        password: null,
                        firstname: null,
                        lastname: null
                    },
                    contact: {
                        email: null,
                        tel: null,
                        mobile: null
                    }
                } 
            }

        }

    }
}
</script>